import { DashboardSidebar } from "@/components/dashboard/sidebar"
import { DashboardHeader } from "@/components/dashboard/dashboard-header"
import { UserKPICards } from "@/components/dashboard/user-kpi-cards"
import { UserInventoryTable } from "@/components/dashboard/user-inventory-table"
import { UserAlerts } from "@/components/dashboard/user-alerts"
import { AuthGuard } from "@/components/auth/auth-guard"

export default function UserDashboardPage() {
  return (
    <AuthGuard>
      <div className="flex h-screen bg-background">
        <DashboardSidebar isAdmin={false} />
        <div className="flex flex-1 flex-col overflow-hidden">
          <DashboardHeader title="Facility Dashboard" subtitle="Manage your medicine inventory" />
          <main className="flex-1 overflow-y-auto p-6">
            <div className="space-y-6">
              {/* KPI Cards */}
              <UserKPICards />

              {/* Two Column Layout */}
              <div className="grid gap-6 lg:grid-cols-3">
                <div className="lg:col-span-2">
                  <UserInventoryTable />
                </div>
                <div className="lg:col-span-1">
                  <UserAlerts />
                </div>
              </div>
            </div>
          </main>
        </div>
      </div>
    </AuthGuard>
  )
}
